<template>
  <div>
    <h1>webscoket</h1>
    <el-row>
      <el-button @click="initWebSocket()">开始连接</el-button>
      <el-button type="primary" @click="disconnect()">断开连接</el-button>
      <el-button type="success" @click="send()">发送消息</el-button>
      <el-button type="success" @click="sendTopic()">广播</el-button>
      <el-button type="success" @click="sendAllUser()">像所有人说话</el-button>
      <el-button type="success" @click="sendTopic()">像指定的人说话</el-button>
      <el-input v-model="input"></el-input>
    </el-row>
    <el-row>
      <el-input type="textarea" v-model="message"></el-input>
    </el-row>

  </div>
</template>
<script>
// 安装并引入相关模块
import SockJS from 'sockjs-client';
import Stomp from 'stompjs';
import {
  getToken
} from '@/utils/authentication'
export default {
  data () {
    return {
      input: '',
      message: '',
      dataList: []
    };
  },
  mounted: function () {

  },
  beforeDestroy: function () {
    // 页面离开时断开连接,清除定时器
    this.disconnect();
    clearInterval(this.timer);
  },
  methods: {
    initWebSocket () {
      this.connection();
      let self = this;
      // 断开重连机制,尝试发送消息,捕获异常发生时重连
      this.timer = setInterval(() => {
        try {
          self.stompClient.send("test");
        } catch (err) {
          console.log("断线了: " + err);
          self.connection();
        }
      }, 5000);
    },
    removeTab (targetName) {
      console.log(targetName)
    },
    connection () {
      // 建立连接对象
      this.socket = new SockJS('http://127.0.0.1:8081/myUrl?token=' + getToken());//连接服务端提供的通信接口，连接以后才可以订阅广播消息和个人消息
      // 获取STOMP子协议的客户端对象
      this.stompClient = Stomp.over(this.socket);
      // 定义客户端的认证信息,按需求配置
      var headers = {
        login: 'mylogin',
        passcode: 'mypasscode',
        // additional header
        'client-id': 'my-client-id'
      };
      // 向服务器发起websocket连接
      this.stompClient.connect(headers, () => {
        this.stompClient.subscribe('/user/queue/sendUser', (msg) => { // 订阅服务端提供的某个topic
          console.log(msg.body);  // msg.body存放的是服务端发送给我们的信息
        });
        this.stompClient.subscribe('/topic/sendTopic', (msg) => { //广播
          console.log(msg)
        });
        console.log("连接成功");
      }, (err) => {
        // 连接发生错误时的处理函数
        console.log("连接失败");
        console.log(err);
      });
    },
    // 断开连接
    disconnect () {
      if (this.stompClient != null) {
        this.stompClient.disconnect();
        console.log("Disconnected");
      }
    }
    , send () {
      // this.$api.webscoket.send();
      this.stompClient.send("/sendUser", {}, JSON.stringify({ name: 'SuperAdmin' }))

    },
    sendTopic () {
      // this.$api.webscoket.sendTopic();
      this.stompClient.send("/sendTopic")
    },
    sendAllUser () {
      this.$api.webscoket.sendAllUser({ message: "大家早上好" });
    },
  }
};
</script>